import { Component, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { TooltipDirective } from "../../../shared/component/tooltip/tooltip.directive";
import { Tooltip } from '../../../shared/component/tooltip/tooltip';
import { TooltipPosition } from '../../../shared/component/tooltip/tooltip.enums';
import { TeamDataService } from '../../service/team-data.service';

@Component({
  selector: 'app-team-config',
  imports: [
    CardComponent,
    FormsModule,
    ReactiveFormsModule,
    TooltipDirective
  ],
  templateUrl: './team-config.component.html',
  styleUrl: './team-config.component.css'
})
export class TeamConfigComponent {

  private readonly teamDataService = inject(TeamDataService);

  protected tooltipData: Tooltip = {
    footer: "",
    paragraphs: ["Refresh team data"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  refreshTeams() {
    this.teamDataService.refreshTeams()
  }

}
