import { Component, computed, effect, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { FormsModule } from '@angular/forms';
import { NgClass } from '@angular/common';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { PitLaneConfigService } from '../../service/pit-lane-config.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-pit-lane-config',
  imports: [
    CardComponent,
    FormsModule,
    NgClass,
    ChipComponent
  ],
  templateUrl: './pit-lane-config.component.html',
  styleUrl: './pit-lane-config.component.css'
})
export class PitLaneConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly pitLaneConfigService = inject(PitLaneConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.pitLaneVisible;
    }
    return false;
  });

  closeBeforeEndRaceMinute = 0;

  constructor() {
    effect(() => {
      const config = this.pitLaneConfigService.pitLaneConfig()
      if (config) {
        this.closeBeforeEndRaceMinute = config.closeBeforeEndRaceMinute;
      }
    });
  }

  updatePitLaneVisibility(){
    this.boardConfigService.updatePitLaneVisibility(!this.visible());
  }

  onSubmitCloseBeforeEndRaceMinute() {
    this.pitLaneConfigService.updateCloseBeforeEndRaceMinute(this.closeBeforeEndRaceMinute);
  }

  increaseCloseBeforeEndRaceMinute() {
    this.pitLaneConfigService.updateCloseBeforeEndRaceMinute(this.closeBeforeEndRaceMinute + 1);
  }

  decreaseCloseBeforeEndRaceMinute() {
    this.pitLaneConfigService.updateCloseBeforeEndRaceMinute(this.closeBeforeEndRaceMinute - 1);
  }
}
