import { Component, computed, effect, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { NgClass } from '@angular/common';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { KartConfigService } from '../../service/kart-config.service';
import { FormsModule } from '@angular/forms';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-kart-change-config',
  imports: [
    CardComponent,
    NgClass,
    FormsModule,
    ChipComponent
  ],
  templateUrl: './kart-change-config.component.html',
  styleUrl: './kart-change-config.component.css'
})
export class KartChangeConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly kartConfigService = inject(KartConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.kartChangeVisible;
    }
    return false;
  });

  minKartChange: number = 0;
  maxKartChange: number = 0;

  constructor() {
    effect(() => {
      const config = this.kartConfigService.kartConfig()
      if (config) {
        this.minKartChange = config.minKartChange;
        this.maxKartChange = config.maxKartChange;
      }
    });
  }

  updateKartChangeVisibility(){
    this.boardConfigService.updateKartChangeVisibility(!this.visible());
  }

  // MIN KART CHANGE
  onSubmitMinKartChange() {
    this.kartConfigService.updateMinKartChange(this.minKartChange);
  }

  increaseMinKartChange() {
    this.kartConfigService.updateMinKartChange(this.minKartChange + 1);
  }

  decreaseMinKartChange() {
    this.kartConfigService.updateMinKartChange(this.minKartChange - 1);
  }

  // MAX KART CHANGE
  onSubmitMaxKartChange(){
    this.kartConfigService.updateMaxKartChange(this.maxKartChange - 1);
  }

  increaseMaxKartChange() {
    this.kartConfigService.updateMaxKartChange(this.maxKartChange + 1);
  }

  decreaseMaxKartChange() {
    this.kartConfigService.updateMaxKartChange(this.maxKartChange - 1);
  }
}
