import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { RaceService } from '../../race/service/race.service';
import { NotificationService } from '../../shared/service/notification.service';
import { LiveTimingExtractionUpdateService } from './live-timing-extraction-update.service';
import { LiveTimingExtraction } from '../model/live-timing-extraction';
import { combineLatest, map, Observable, of, switchMap } from 'rxjs';
import { environment } from '../../../environments/environment';
import { takeUntilDestroyed, toObservable } from '@angular/core/rxjs-interop';
import { LiveTimingConfigService } from './live-timing-config.service';
import { LapMissing } from '../../lap-missing/model/lap-missing';

@Injectable({
  providedIn: 'root'
})
export class LiveTimingExtractionDataService {

  private readonly path: string = '/live-timing-extraction-management';
  private readonly httpClient = inject(HttpClient);

  private readonly liveTimingExtractionUpdateService = inject(LiveTimingExtractionUpdateService);
  private readonly raceService = inject(RaceService);
  private readonly notificationService = inject(NotificationService);
  private readonly liveTimingConfigService = inject(LiveTimingConfigService);

  readonly liveTimingExtraction: WritableSignal<LiveTimingExtraction | undefined> = signal(undefined);

  constructor() {
    // Handle race changes
    combineLatest({
      activeRace: toObservable(this.raceService.activeRace)
    })
    .pipe(
      takeUntilDestroyed(),
      switchMap(({activeRace}) => {
        if (activeRace) {
          return this.getActiveLiveTimingExtraction(activeRace.id);
        }
        return of(undefined);
      })
    ).subscribe(fetchedLiveTimingExtraction => {
      this.liveTimingExtraction.set(fetchedLiveTimingExtraction);
    });

    toObservable(this.liveTimingExtractionUpdateService.liveTimingExtractionUpdate)
    .pipe(
      takeUntilDestroyed(),
      switchMap((liveTimingExtractionUpdate) => {
        if (!liveTimingExtractionUpdate) {
          return of({
            fetchedLiveTimingExtraction: undefined,
            deletedId: undefined
          });
        }

        // If there are updated IDs, fetch the updated data
        if (liveTimingExtractionUpdate.updatedId) {
          return this.getActiveLiveTimingExtraction(liveTimingExtractionUpdate.raceId).pipe(
            map(fetchedLiveTimingExtraction => ({
              fetchedLiveTimingExtraction,
              deletedId: liveTimingExtractionUpdate.deletedId
            }))
          );
        }

        // If only deletions, return empty fetched data with deleted IDs
        return [{
          fetchedLiveTimingExtraction: undefined,
          deletedId: liveTimingExtractionUpdate.deletedId
        }];
      })
    ).subscribe(result => {
      const currentLiveTimingExtraction = this.liveTimingExtraction();
      if (result.fetchedLiveTimingExtraction) {
        this.liveTimingExtraction.set(result.fetchedLiveTimingExtraction);
      } else if (result.deletedId && currentLiveTimingExtraction?.id === result.deletedId) {
        this.liveTimingExtraction.set(undefined);
      }
    });
  }

  public refreshActiveLiveTimingExtraction(): void {
    const activeRace = this.raceService.activeRace();

    if (activeRace === undefined) {
      this.notificationService.info('No active race')
      return;
    }

    this.getActiveLiveTimingExtraction(activeRace.id)
    .subscribe(fetchedLiveTimingExtraction => {
      this.liveTimingExtraction.set(fetchedLiveTimingExtraction);
      this.notificationService.success('Live timing extraction refreshed')
    });
  }

  public startExtraction(): void {
    const activeRace = this.raceService.activeRace();
    const config = this.liveTimingConfigService.liveTimingConfig();

    if (activeRace === undefined) {
      this.notificationService.info('No active race')
      return;
    }

    if (config) {
      this.startLiveTimingExtraction(activeRace.id, config.source, config.url)
      .subscribe(() => {
        this.notificationService.success('Live timing extraction started')
      });
    }
  }

  public stopExtraction(): void {
    const activeLiveTimingExtraction = this.liveTimingExtraction();

    if (activeLiveTimingExtraction === undefined) {
      this.notificationService.info('No live timing extraction is running')
      return;
    }

    this.stopLiveTimingExtraction(activeLiveTimingExtraction.id)
    .subscribe(() => {
      this.notificationService.success('Live timing extraction stopped')
    });
  }

  public forceExtraction(): void {
    const activeLiveTimingExtraction = this.liveTimingExtraction();

    if (activeLiveTimingExtraction === undefined) {
      this.notificationService.info('No live timing extraction is running')
      return;
    }

    this.forceLiveTimingExtraction(activeLiveTimingExtraction.id)
    .subscribe(() => {
      this.notificationService.success('Live timing extraction forced')
    });
  }

  public testCloudTaskQueue(): void {
    this.httpClient.get<LapMissing[]>(
      `${environment.baseUrl}${this.path}/test-cloud-task-queue`,
    )
    .subscribe(result => {
      this.notificationService.success('Cloud task queue test successful');
    });
  }

  private getActiveLiveTimingExtraction(raceId: number): Observable<LiveTimingExtraction | undefined> {
    return this.httpClient
    .get<LiveTimingExtraction>(
      `${environment.baseUrl}${this.path}/active-live-timing-extraction/${raceId}`,
    )
    .pipe(
      map((result) => result ? result as LiveTimingExtraction : undefined));
  }

  private startLiveTimingExtraction(
    raceId: number,
    source: string,
    url: string,
  ): Observable<LiveTimingExtraction | undefined> {
    return this.httpClient
    .post<LiveTimingExtraction>(
      `${environment.baseUrl}${this.path}/start`,
      {
        raceId: raceId,
        source: source,
        webSiteUrl: url,
      }
    ).pipe(
      map((result) => result ? result as LiveTimingExtraction : undefined)
    );
  }

  private stopLiveTimingExtraction(id: number): Observable<void> {
    return this.httpClient
    .post<void>(
      `${environment.baseUrl}${this.path}/stop/${id}`,
      {}
    );
  }

  private forceLiveTimingExtraction(id: number): Observable<void> {
    return this.httpClient
    .post<void>(
      `${environment.baseUrl}${this.path}/force-extraction/${id}`,
      {}
    );
  }
}
