import { Component, computed, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { NgClass } from '@angular/common';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-driver-performance-config',
  imports: [
    CardComponent,
    NgClass,
    ChipComponent
  ],
  templateUrl: './driver-performance-config.component.html',
  styleUrl: './driver-performance-config.component.css'
})
export class DriverPerformanceConfigComponent {
  private readonly boardConfigService = inject(BoardConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.driverPerformanceVisible;
    }
    return false;
  });

  updateDriverPerformanceVisibility() {
    this.boardConfigService.updateDriverPerformanceVisibility(!this.visible());
  }
}
