import { Component, computed, effect, inject } from '@angular/core';
import { FormsModule } from "@angular/forms";
import { LiveTimingConfigService } from "../../service/live-timing-config.service";
import { NgClass, NgForOf } from "@angular/common";
import { RaceService } from "../../../race/service/race.service";
import { LiveTimingService } from "../../service/live-timing.service";
import { CardComponent } from '../../../shared/component/card/card.component';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { LiveTimingExtractionDataService } from '../../service/live-timing-extraction-data.service';
import { TooltipDirective } from '../../../shared/component/tooltip/tooltip.directive';
import { Tooltip } from '../../../shared/component/tooltip/tooltip';
import { TooltipPosition } from '../../../shared/component/tooltip/tooltip.enums';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-live-timing-config',
  imports: [
    FormsModule,
    NgForOf,
    CardComponent,
    NgClass,
    TooltipDirective,
    ChipComponent
  ],
  templateUrl: './live-timing-config.component.html',
  styleUrl: './live-timing-config.component.css'
})
export class LiveTimingConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly liveTimingConfigService = inject(LiveTimingConfigService);
  private readonly liveTimingService = inject(LiveTimingService);
  private readonly raceService = inject(RaceService);
  private readonly liveTimingExtractionDataService = inject(LiveTimingExtractionDataService);

  protected sources = this.liveTimingService.sources;

  isActiveRace = computed(() => {
    return this.raceService.activeRace() !== undefined;
  });

  isActiveLiveTimingExtraction = computed(() => {
    return this.liveTimingExtractionDataService.liveTimingExtraction() !== undefined;
  });

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.liveTimingVisible;
    }
    return false;
  });

  selectedSource: string = '';
  url: string = '';

  protected tooltipData: Tooltip = {
    footer: "",
    paragraphs: ["Refresh active live timing extraction data"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  constructor() {
    effect(() => {
      const config = this.liveTimingConfigService.liveTimingConfig()
      if (config) {
        this.selectedSource = config.source;
        this.url = config.url;
      }
    });
  }

  updateLiveTimingVisibility(){
    this.boardConfigService.updateLiveTimingVisibility(!this.visible());
  }

  onSubmitUrl() {
    this.liveTimingConfigService.updateUrl(this.url.trim());
  }

  onSubmitSource() {
    this.liveTimingConfigService.updateSource(this.selectedSource.trim());
  }

  refreshActiveLiveTimingExtraction(){
    this.liveTimingExtractionDataService.refreshActiveLiveTimingExtraction();
  }

  startLiveTimingExtraction(){
    this.liveTimingExtractionDataService.startExtraction();
  }

  stopLiveTimingExtraction(){
    this.liveTimingExtractionDataService.stopExtraction();
  }

  forceLiveTimingExtraction(){
    this.liveTimingExtractionDataService.forceExtraction();
  }

  testCloudTaskQueue(){
    this.liveTimingExtractionDataService.testCloudTaskQueue();
  }
}
