import { Component, computed, effect, inject } from '@angular/core';
import { FormsModule } from "@angular/forms";
import { BoardConfigService } from "../../../board/service/board-config.service";
import { DriverConfigService } from "../../service/driver-config.service";
import { NgClass } from "@angular/common";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-driver-change-config',
  imports: [
    FormsModule,
    NgClass,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './driver-change-config.component.html',
  styleUrl: './driver-change-config.component.css'
})
export class DriverChangeConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly driverConfigService = inject(DriverConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.driverChangeVisible;
    }
    return false;
  });

  minDriverChange = 0;

  constructor() {
    effect(() => {
      const config = this.driverConfigService.driverConfig()
      if (config) {
        this.minDriverChange = config.minDriverChange;
      }
    });
  }

  // DRIVER CHANGE
  onSubmitMinDriverChange() {
    this.driverConfigService.updateMinDriverChange(this.minDriverChange);
  }

  decreaseMinDriverChange() {
    this.driverConfigService.updateMinDriverChange(this.minDriverChange - 1);
  }

  increaseMinDriverChange() {
    this.driverConfigService.updateMinDriverChange(this.minDriverChange + 1);
  }

  updateDriverChangeVisibility(){
    this.boardConfigService.updateDriverChangeVisibility(!this.visible());
  }
}
