import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { Observable } from "rxjs";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { takeUntilDestroyed } from "@angular/core/rxjs-interop";

@Injectable({
  providedIn: 'root'
})
export class LiveTimingService {
  private readonly path: string = '/live-timing-extraction-management';
  private readonly httpClient = inject(HttpClient);

  readonly sources: WritableSignal<string[]> = signal([]);

  constructor() {
    this.getSources()
    .pipe(takeUntilDestroyed())
    .subscribe(sources => {
      this.sources.set(sources);
    });
  }

  private getSources(): Observable<string[]> {
    return this.httpClient
    .get<string[]>(`${environment.baseUrl}${this.path}/sources`);
  }
}
