import { Component, computed, effect, inject } from '@angular/core';
import { FormsModule } from "@angular/forms";
import { BoardConfigService } from "../../../board/service/board-config.service";
import { RaceConfigService } from "../../../race/service/race-config.service";
import { NgClass } from "@angular/common";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-fuel-config',
  imports: [
    FormsModule,
    NgClass,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './fuel-config.component.html',
  styleUrl: './fuel-config.component.css'
})
export class FuelConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly raceConfigService = inject(RaceConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.fuelVisible;
    }
    return false;
  });

  fuelDurationMinute = 0;

  constructor() {
    effect(() => {
      const raceConfig = this.raceConfigService.raceConfig()
      if (raceConfig) {
        this.fuelDurationMinute = raceConfig.fuelDurationMinute;
      }
    });
  }

  updateFuelVisibility(){
    this.boardConfigService.updateFuelVisibility(!this.visible());
  }

  onSubmitFuelDuration() {
    this.raceConfigService.updateFuelDurationMinute(this.fuelDurationMinute);
  }

  increaseFuelDuration() {
    this.raceConfigService.updateFuelDurationMinute(this.fuelDurationMinute + 1);
  }

  decreaseFuelDuration() {
    this.raceConfigService.updateFuelDurationMinute(this.fuelDurationMinute - 1);
  }
}
