import { Component, computed, effect, inject } from '@angular/core';
import { FormsModule } from "@angular/forms";
import { NgClass } from "@angular/common";
import { BoardConfigService } from "../../../board/service/board-config.service";
import { KartConfigService } from "../../service/kart-config.service";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-kart-config',
  imports: [
    FormsModule,
    NgClass,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './kart-config.component.html',
  styleUrl: './kart-config.component.css'
})
export class KartConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly kartConfigService = inject(KartConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.kartVisible;
    }
    return false;
  });

  maxKartMinute = 0;

  constructor() {
    effect(() => {
      const config = this.kartConfigService.kartConfig()
      if (config) {
        this.maxKartMinute = config.maxKartMinute;
      }
    });
  }

  updateKartVisibility(){
    this.boardConfigService.updateKartVisibility(!this.visible());
  }

  onSubmitKartMinute() {
    this.kartConfigService.updateMaxKartMinute(this.maxKartMinute);
  }

  increaseKartMinute() {
    this.kartConfigService.updateMaxKartMinute(this.maxKartMinute + 1);
  }

  decreaseKartMinute() {
    this.kartConfigService.updateMaxKartMinute(this.maxKartMinute - 1);
  }
}
