import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest, map, Observable, takeUntil } from "rxjs";
import { RaceService } from "../../race/service/race.service";
import { FirestoreService } from "../../shared/service/firestore.service";
import { collection, collectionData } from "@angular/fire/firestore";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { LiveTimingExtractionUpdate } from '../model/live-timing-extraction-update';

@Injectable({
  providedIn: 'root'
})
export class LiveTimingExtractionUpdateService extends FirestoreService {
  protected override collectionPath: string = '/live-timing-extraction-update';
  protected override collectionRef = collection(this.firestore, this.collectionPath);

  private readonly raceService = inject(RaceService);

  readonly liveTimingExtractionUpdate: WritableSignal<LiveTimingExtractionUpdate | undefined> = signal(undefined)

  constructor() {
    super();
    combineLatest({
      liveTimingExtractionUpdate: this.getLiveTimingExtractionUpdate()
    }).pipe(takeUntilDestroyed())
    .subscribe(({liveTimingExtractionUpdate}) => {
      if (liveTimingExtractionUpdate) {
        const actual = this.liveTimingExtractionUpdate();
        if (!actual || actual.updatedAt < liveTimingExtractionUpdate.updatedAt) {
          this.liveTimingExtractionUpdate.set(liveTimingExtractionUpdate);
        }
      }
    });
  }

  private getLiveTimingExtractionUpdate(): Observable<LiveTimingExtractionUpdate | undefined> {
    return combineLatest({
      updates: collectionData(this.collectionRef),
      activeRace: toObservable(this.raceService.activeRace)
    })
    .pipe(
        takeUntil(this.destroyed),
        map(({updates, activeRace}) => {
          const result = updates as LiveTimingExtractionUpdate[];
          if (activeRace && result.length > 0) {
            return result
            .filter(update => !update.deleted && update.raceId === activeRace.id)
            .at(0);
          }
          return undefined;
        })
    );
  }
}
