import { Component, computed, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { NgClass } from '@angular/common';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-stint-optimizer-config',
  imports: [
    CardComponent,
    NgClass,
    ChipComponent
  ],
  templateUrl: './stint-optimizer-config.component.html',
  styleUrl: './stint-optimizer-config.component.css'
})
export class StintOptimizerConfigComponent {
  private readonly boardConfigService = inject(BoardConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.stintOptimizerVisible;
    }
    return false;
  });

  updateStintOptimizerVisibility() {
    this.boardConfigService.updateStintOptimizerVisibility(!this.visible());
  }
}
