import { Component, effect, inject } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { LapConfigService } from "../../service/lap-config.service";
import { LapDataService } from "../../service/lap-data.service";
import { CardComponent } from '../../../shared/component/card/card.component';
import { LapRefreshUpdateService } from '../../service/lap-refresh-update.service';
import { TooltipDirective } from '../../../shared/component/tooltip/tooltip.directive';
import { Tooltip } from '../../../shared/component/tooltip/tooltip';
import { TooltipPosition } from '../../../shared/component/tooltip/tooltip.enums';

@Component({
  selector: 'app-lap-config',
  imports: [
    FormsModule,
    ReactiveFormsModule,
    CardComponent,
    TooltipDirective
  ],
  templateUrl: './lap-config.component.html',
  styleUrl: './lap-config.component.css'
})
export class LapConfigComponent {
  private readonly lapConfigService = inject(LapConfigService);
  private readonly lapDataService = inject(LapDataService);
  private readonly lapRefreshUpdateService = inject(LapRefreshUpdateService);

  referenceLapTime = 0
  paceLapTimeThreshold = 0;
  paceLapNumber = 0;

  protected tooltipData: Tooltip = {
    footer: "",
    paragraphs: ["Refresh laps data"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  constructor() {
    effect(() => {
      const lapConfig = this.lapConfigService.lapConfig()
      if (lapConfig) {
        this.referenceLapTime = lapConfig.referenceLapTimeMillisecond;
        this.paceLapTimeThreshold = lapConfig.paceLapThresholdMilliseconds;
        this.paceLapNumber = lapConfig.paceLapNumber;
      }
    });
  }

  // REFERENCE LAP TIME
  onSubmitReferenceLapTime() {
    this.lapConfigService.updateReferenceLapTime(this.referenceLapTime);
  }

  // PACE LAP TIME THRESHOLD
  onSubmitPaceLapTimeThreshold() {
    this.lapConfigService.updatePaceLapThreshold(this.paceLapTimeThreshold);
  }

  // PACE LAP NUMBER
  onSubmitPaceLapNumber() {
    this.lapConfigService.updatePaceLapNumber(this.paceLapNumber);
  }

  increasePaceLapNumber() {
    this.lapConfigService.updatePaceLapNumber(this.paceLapNumber + 1);
  }

  decreasePaceLapNumber() {
    this.lapConfigService.updatePaceLapNumber(this.paceLapNumber - 1);
  }

  public refreshLaps(): void {
    this.lapRefreshUpdateService.updateRefresh();
    this.lapDataService.refreshLaps();
  }
}
