import { Component, computed, effect, inject } from '@angular/core';
import { TimelineConfigService } from '../../service/timeline-config.service';
import { CardComponent } from '../../../shared/component/card/card.component';
import { FormsModule } from '@angular/forms';
import { NgClass } from '@angular/common';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-timeline-config',
  imports: [
    CardComponent,
    FormsModule,
    NgClass,
    ChipComponent
  ],
  templateUrl: './timeline-config.component.html',
  styleUrl: './timeline-config.component.css'
})
export class TimelineConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly timelineConfigService = inject(TimelineConfigService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.timelineVisible;
    }
    return false;
  });

  visibleAfterMinWidthPixel = 0;

  constructor() {
    effect(() => {
      const config = this.timelineConfigService.timelineConfig();
      if (config) {
        this.visibleAfterMinWidthPixel = config.visibleAfterMinWidthPixel;
      }
    });
  }

  updateTimelineVisibility(){
    this.boardConfigService.updateTimelineVisibility(!this.visible());
  }

  onSubmitVisibleAfterMinWidthPixel(){
    this.timelineConfigService.updateVisibleAfterMinWidthPixel(this.visibleAfterMinWidthPixel);
  }

  increaseVisibleAfterMinWidthPixel(){
    this.timelineConfigService.updateVisibleAfterMinWidthPixel(this.visibleAfterMinWidthPixel + 1);
  }

  decreaseVisibleAfterMinWidthPixel(){
    this.timelineConfigService.updateVisibleAfterMinWidthPixel(this.visibleAfterMinWidthPixel - 1);
  }

}
