import { Component, computed, effect, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { NgClass } from '@angular/common';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { FormsModule } from '@angular/forms';
import { RaceConfigService } from '../../../race/service/race-config.service';
import { TooltipDirective } from '../../../shared/component/tooltip/tooltip.directive';
import { StintDataService } from '../../service/stint-data.service';
import { Tooltip } from '../../../shared/component/tooltip/tooltip';
import { TooltipPosition } from '../../../shared/component/tooltip/tooltip.enums';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-stint-config',
  imports: [
    CardComponent,
    NgClass,
    FormsModule,
    TooltipDirective,
    ChipComponent
  ],
  templateUrl: './stint-config.component.html',
  styleUrl: './stint-config.component.css'
})
export class StintConfigComponent {

  private readonly boardConfigService = inject(BoardConfigService);
  private readonly raceConfigService = inject(RaceConfigService);
  private readonly stintDataService = inject(StintDataService);

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.stintVisible;
    }
    return false;
  });

  minStintMinute = 0;

  protected tooltipData: Tooltip = {
    footer: "",
    paragraphs: ["Refresh stint data"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;


  constructor() {
    effect(() => {
      const config = this.raceConfigService.raceConfig()
      if (config) {
        this.minStintMinute = config.minStintMinute;
      }
    });
  }

  refreshStints() {
    this.stintDataService.refreshStints();
  }

  updateStintVisibility() {
    this.boardConfigService.updateStintVisibility(!this.visible());
  }

  onSubmitMinStintMinute() {
    this.raceConfigService.updateMinStintMinute(this.minStintMinute);
  }

  increaseMinStintMinute() {
    this.raceConfigService.updateMinStintMinute(this.minStintMinute + 1);
  }

  decreaseMinStintMinute() {
    this.raceConfigService.updateMinStintMinute(this.minStintMinute - 1);
  }
}
