import { Component, computed, inject } from '@angular/core';
import { BoardConfigService } from '../../../board/service/board-config.service';
import { CardComponent } from '../../../shared/component/card/card.component';
import { NgClass } from '@angular/common';
import { LapMissingDataService } from '../../service/lap-missing-data.service';
import { TooltipDirective } from '../../../shared/component/tooltip/tooltip.directive';
import { Tooltip } from '../../../shared/component/tooltip/tooltip';
import { TooltipPosition } from '../../../shared/component/tooltip/tooltip.enums';
import { ChipComponent } from '../../../shared/component/chip/chip.component';
import { LapMissingService } from '../../service/lap-missing.service';

@Component({
  selector: 'app-lap-missing-config',
  imports: [
    CardComponent,
    NgClass,
    TooltipDirective,
    ChipComponent
  ],
  templateUrl: './lap-missing-config.component.html',
  styleUrl: './lap-missing-config.component.css'
})
export class LapMissingConfigComponent {
  private readonly boardConfigService = inject(BoardConfigService);
  private readonly lapMissingDataService = inject(LapMissingDataService);
  private readonly lapMissingService = inject(LapMissingService);

  lapMissingCount = this.lapMissingService.lapMissingCount;

  visible = computed(() => {
    const config = this.boardConfigService.boardConfig();
    if (config) {
      return config.lapMissingVisible;
    }
    return false;
  });

  protected tooltipData: Tooltip = {
    footer: "",
    paragraphs: ["Refresh lap missing data"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  updateMissingLapVisibility(){
    this.boardConfigService.updateLapMissingVisibility(!this.visible());
  }

  public forceCheckLapMissing(): void {
    this.lapMissingDataService.forceCheckLapMissing();
  }

  public refreshLapMissing(): void {
    this.lapMissingDataService.refreshLapMissing();
  }

  public testCloudTaskQueue(): void {
    this.lapMissingDataService.testCloudTaskQueue();
  }
}
