import { Component, computed, effect, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import { FormsModule } from '@angular/forms';
import { LapLoadingConfigService } from '../../service/lap-loading-config.service';
import { LapDataService } from '../../service/lap-data.service';
import { LapService } from '../../service/lap.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-lap-loading-config',
  imports: [
    CardComponent,
    FormsModule,
    ChipComponent
  ],
  templateUrl: './lap-loading-config.component.html',
  styleUrl: './lap-loading-config.component.css'
})
export class LapLoadingConfigComponent {
  private readonly lapLoadingConfigService = inject(LapLoadingConfigService);
  private readonly lapDataService = inject(LapDataService);
  private readonly lapService = inject(LapService);

  loadedLaps = computed(() => {
    return this.lapDataService.laps().length
  });

  pittedLaps = computed(() => {
    return this.lapService.pitLaps().length;
  })

  pageSize = 0;

  constructor() {
    effect(() => {
      const lapLoadingConfig = this.lapLoadingConfigService.lapLoadingConfig();
      if (lapLoadingConfig) {
        this.pageSize = lapLoadingConfig.pageSize;
      }
    });
  }

  // PAGE SIZE DATA LOADING
  onSubmitPageSize() {
    this.lapLoadingConfigService.updatePageSize(this.pageSize);
  }

  reloadAllLaps() {
    this.lapDataService.reloadAllLaps();
  }
}
